@import '../../../assets/common-scss/common.scss';
.settings{
    float: right;
    position: relative;
    cursor: pointer;
    
    .settings-submenu{
        position: absolute;
        width: 220px;
        right: 0;
        background: white;
        padding: 5px 10px 5px 10px;
        z-index: 999;
        @include shadow();

        .setting-item{
            cursor: pointer;
            display: flex;
            gap: 7px;
            padding: 5px 0;
            .label{
                cursor: inherit;
                margin-top: 3px;
            }
            &:hover{
                color: rgb(3, 13, 34);
                font-weight: 450;
            }
        }
    }
    @media screen and (max-width: 720px) {
        margin-top: -15px;
        
    }
}