@import '../../../assets/common-scss/common.scss';

.results{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap:20px;

    .status-button{
        text-align: center;
        position: relative;
        padding: 25px;
        border: 1px solid;
        border-radius: 15px;
        color: white;
        font-weight: bold;
        overflow: hidden;
        &.success{
            background-color: green;
        }
        &.error{
            background-color: #f71616e0;
        }
        
        .title{
            padding: 5px 5px 5px 12px;
            position: absolute;
            top: -12px;
            left: 0;
            font-size: 12px;
            width: 100%;
            text-align: left;
            background: $headingColor;
            color: white;
        }
        .widget-content{
            padding-top: 15px;
        }
    }
    @media screen and (max-width: 720px){
        grid-template-columns: 1fr 1fr;
    }
}